* {
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  list-style: none;
  transition: all .15s ease-in-out;
}

body {
  background-color: #000;
  padding: 0 20px;
}

.button-list {
  margin: 20px 0;
  display: flex;
}

input {
  color: #000;
}

button {
  color: #fff;
  width: 110px;
  background-color: #000;
  border: none;
  border-radius: 5px;
  margin-right: 5px;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
}

.main-button {
  background-color: #2d2d2d;
}

button:hover {
  transform: scale(1.1);
}

.main-button:first-letter {
  color: red;
}

.main {
  background-color: #2d2d2d;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 25px;
}

.message {
  height: 50px;
  width: 50px;
  background-image: url("done.f29849bb.svg");
}

